<template>
  <div>
    APP  KMS TEST!!
    <!--   AWS KMS /   -->
  </div>
</template>

<script>
import {
  KmsKeyringBrowser,
  KMS,
  getClient,
  buildClient,
  CommitmentPolicy,
} from '@aws-crypto/client-browser'
import { toBase64, fromBase64 } from '@aws-sdk/util-base64-browser'
import AWS from 'aws-sdk'

// AWS KMS  
const { encrypt, decrypt } = buildClient(
  CommitmentPolicy.REQUIRE_ENCRYPT_REQUIRE_DECRYPT
)

// KMS CMK 
const generatorKeyId = 'arn:aws:kms:ap-northeast-2:246233014480:alias/medibuddy-key' // KMS KEY 
const keyIds = ['arn:aws:kms:ap-northeast-2:246233014480:key/mrk-36248b8bc7e94d49b03f0fbe14b10148'] // KMS KEY ARN

export default {
  name: 'AwsKms',
  data () {
    return {
      context: { // EncryptionContext :    ( )
        company: 'hecto-clinic', // 
        sever: process.env.NODE_ENV, // 
        accountType: '', //  
        accountId: '' //  
      },
    }
  },
  async created() {
    console.log(process.env.NODE_ENV)

    //      
    window.vue.kmsEncode = this.encode
    window.vue.kmsDecode = this.decode

    // AWS Congnito   (API ) -      !
    let url = process.env.VUE_APP_MESSAGE_API_URL + '/awsAuth/getToken'
    let result = await this.sendPost(url)
    console.log(result)
    if(result.RESULT_CODE == "0"){
      //   - Amazon Cognito
      AWS.config.region = 'ap-northeast-2'; // 
      AWS.config.credentials = new AWS.CognitoIdentityCredentials({
        IdentityPoolId: result.DATA.identityPoolId,
        IdentityId: result.DATA.identityId,
        Logins: {
          'cognito-identity.amazonaws.com': result.DATA.token
        }
      });
      
      // EncryptionContext 
      this.context.accountType = result.DATA.accountType //  
      this.context.accountId = result.DATA.accountId //  
    } else {
      console.log("[error] " + result.RESULT_CODE + " / " + result.RESULT_MESSAGE)
    }
  },
  methods: {
    //  KMS 
    async encode (key) {
      // 
      if(!key){
        this.$root.toast("  .")
        return
      }
      // AWS KMS  
      const clientProvider = getClient(KMS, {
          credentials: AWS.config.credentials
      })
      //  
      const keyring = new KmsKeyringBrowser({
          clientProvider,
          generatorKeyId,
          keyIds
      })
      console.log(keyring)

      //  unit8array 
      const plaintext = new Uint8Array(Buffer.from(key, 'utf8'))
      
      // 
      const { result } = await encrypt(keyring, plaintext, { encryptionContext: this.context })
      
      //  (Base64), EncryptionContext 
      let ret = {
        key: toBase64(result),
        context: JSON.stringify(this.context)
      }
      return ret
    },
    //  KMS 
    async decode(key, context) {
      // 
      if(!key){
        alert('   .')
        return
      }
      // AWS KMS  
      const clientProvider = getClient(KMS, {
          credentials: AWS.config.credentials
      })
      //  
      const keyring = new KmsKeyringBrowser({
          clientProvider,
          generatorKeyId,
          keyIds
      })
      
      // 
      const { plaintext, messageHeader } = await decrypt(keyring, fromBase64(key))
      const { encryptionContext } = messageHeader
      
      //    (    )
      Object
        .entries(JSON.parse(context))
        .forEach(([key, value]) => {
          if (encryptionContext[key] !== value) throw new Error('Encryption Context does not match expected values')
        })

      //   
      let ret = {
        key: String.fromCharCode.apply(null, new Uint8Array(plaintext))
      }
      return ret
    }
  }
}
</script>

